body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-family: "OpenSans", "Verdana", sans-serif;
  font-size: 17px;
  line-height: 24px;
  text-align: left;
  color: #444444;
  font-style: normal;
}

.button {
  display: block;
  box-sizing: border-box;
  height: 52px;

  padding-top: 14px;
  padding-bottom: 14px;

  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  text-transform: uppercase;
  color: #444444;

  background-color: #ffffff;
  border: solid 2px #444444;

  cursor: pointer;

  &:hover {
    color: #ffffff;

    background-color: #444444;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.modal {
  position: fixed;
  display: none;

  z-index: 10;
}

.modal-show {
  display: block;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;

  width: 100%;
  height: 100%;

  background-color: rgba(255, 255, 255, 0.9);
}

.overlay-show {
  display: block;
}
