.social-list {
  display: flex;
  justify-content: space-between;

  width: 145px;
  margin: 0 auto;
  padding: 0;

  list-style: none;
  border-bottom: 1px solid #ececec;
}

.social-list__item {
  margin-top: 30px;
  margin-bottom: 22px;

  font-size: 0;
}

.social-list__link {
  display: block;

  width: 23px;
  height: 28px;

  background-repeat: no-repeat;

  &--instagram {
    height: 21px;
  }

  &--fb {
    width: 15px;
  }

  &--twitter {
    height: 20px;
  }

  &:hover {
    fill: #46c1ae;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .social-list {
    justify-content: space-between;
    align-items: flex-end;

    margin: 0;

    border: none;
  }

  .social-list__item {
    margin: 0;
  }
}
